import React from "react";

import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Grid from "@material-ui/core/Grid";
import IconButton from "@material-ui/core/IconButton";
import { withStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import AddIcon from "@material-ui/icons/Add";
import RemoveIcon from "@material-ui/icons/Remove";
import { IProduct } from "../types";

const styles: any = (theme: any) => ({
  card: {
    marginTop: theme.spacing.unit,
    marginBottom: theme.spacing.unit,
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit
  },
  content: {
    flex: "1 0 auto",
    paddingBottom: "4px!important",
    minHeight: 80
  },
  icon: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    height: 3 * theme.spacing.unit,
    width: 3 * theme.spacing.unit
  }
});

interface IProps {
  product: IProduct;
  count: number;
  classes: any;
  onChange: (id: string, count: number) => void;
}

class CheckOutCard extends React.Component<IProps> {
  public render() {
    const { product, classes, count, onChange } = this.props;

    return (
      <Card
        classes={{
          root: classes.card
        }}
      >
        <CardContent
          classes={{
            root: classes.content
          }}
        >
          <Grid
            container
            direction="column"
            justify="space-between"
            alignItems="flex-start"
            className={classes.content}
          >
            <Grid item container>
              <Grid item xs={8}>
                <Typography variant="title">{product.title}</Typography>
              </Grid>
              <Grid item xs={4}>
                <div />
              </Grid>
            </Grid>
            <Grid item container alignItems="center" justify="space-between">
              <Grid item xs={8}>
                <Typography variant="body1">{product.size}</Typography>
              </Grid>
              <Grid item xs={4} style={{ textAlign: "right" }}>
                <IconButton
                  component="span"
                  className={classes.icon}
                  onClick={() => onChange(product.id, count - 1)}
                >
                  <RemoveIcon />
                </IconButton>
                {count}
                <IconButton
                  component="span"
                  className={classes.icon}
                  onClick={() => onChange(product.id, count + 1)}
                >
                  <AddIcon />
                </IconButton>
              </Grid>
            </Grid>
          </Grid>
        </CardContent>
      </Card>
    );
  }
}

export default withStyles(styles)(CheckOutCard);
